<template>
  <div class="v-top" style="position: fixed;" :style="{bottom:defaultBottom+'px',right:defaultRight+'px'}" v-show="showIcon">
    <i :class="types[defaultType]" :style="{fontSize:defaultSize+'px',color:defaultColor}" @click="goTop()"></i>
  </div>
</template>
<script>
export default {
  props: ['type','top','right','bottom','size','color','duration'],
  data () {
    return {
      showIcon: false,
      scrollTop: 0,
      types: ['iconfont icon-fanhuidingbu9','iconfont icon-fanhuidingbu8','iconfont icon-stack','iconfont icon-fanhuidingbu7','iconfont icon-fanhuidingbu6','iconfont icon-fanhuidingbu5','iconfont icon-control101','iconfont icon-fanhuidingbu41','iconfont icon-fanhuidingbu3','iconfont icon-fanhuidingbu11','iconfont icon-fanhuidingbu2','iconfont icon-fanhuidingbu1','iconfont icon-4fanhuidingbucheng','iconfont icon-fanhuidingbu','iconfont icon-fanhuidingbu4','iconfont icon-diantifanhuidingbu'],
      defaultType: 0,
      defaultTop: 400,
      defaultRight: 30,
      defaultBottom: 30,
      defaultSize: 24,
      defaultColor: '#666',
      defaultDuration: 500
    }
  },
  beforeDestroy () {
    window.removeEventListener("scroll",this.handelScroll, false)
  },
  mounted () {
    this.type ? this.defaultType = this.type: '';
    this.top ?  this.defaultTop = this.top: '';
    this.right ? this.defaultRight = this.right: '';
    this.bottom ? this.defaultBottom = this.bottom: '';
    this.size ? this.defaultSize = this.size: '';
    this.color ? this.defaultColor = this.color: '';
    this.duration ? this.defaultDuration = this.duration: '';
    window.addEventListener('scroll', this.handelScroll, false)
  },
  methods: {
    handelScroll () {
      this.scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
      this.scrollTop > this.defaultTop ? this.showIcon = true : this.showIcon = false
    },
    goTop () {
      window.requestAnimationFrame = (function(){
        return  window.requestAnimationFrame       ||
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame    ||
          function( callback ){
            window.setTimeout(callback, 1000 / 60);
          };
      })();
      var step =  this.scrollTop / (this.defaultDuration / ( 1000 / 60)) >> 0;
      var self = this
      function fn(){
        if(self.scrollTop >= 0){
          self.scrollTop -= step;
          document.documentElement.scrollTop = document.body.scrollTop = self.scrollTop;
          fn.rafTimer = requestAnimationFrame(fn);
        }else{
          document.body.scrollTop = 0;
          cancelAnimationFrame(fn.rafTimer);
        }
      }
      fn.rafTimer = requestAnimationFrame(fn)
    }
  }
}
</script>
<style>
  @font-face {font-family: "iconfont";
    src: url('//at.alicdn.com/t/font_928263_d29lzpo5kr4.eot?t=1542767986796'); /* IE9*/
    src: url('//at.alicdn.com/t/font_928263_d29lzpo5kr4.eot?t=1542767986796#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
    url('//at.alicdn.com/t/font_928263_d29lzpo5kr4.ttf?t=1542767986796') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
    url('//at.alicdn.com/t/font_928263_d29lzpo5kr4.svg?t=1542767986796#iconfont') format('svg'); /* iOS 4.1- */
  }
  .iconfont {
    font-family:"iconfont" !important;
    font-size:16px;
    cursor: pointer;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .icon-diantifanhuidingbu:before { content: "\e600"; }

  .icon-fanhuidingbu4:before { content: "\e67a"; }

  .icon-fanhuidingbu:before { content: "\e609"; }

  .icon-4fanhuidingbucheng:before { content: "\e85d"; }

  .icon-fanhuidingbu1:before { content: "\e6a2"; }

  .icon-fanhuidingbu2:before { content: "\e63a"; }

  .icon-fanhuidingbu11:before { content: "\e649"; }

  .icon-fanhuidingbu3:before { content: "\e64a"; }

  .icon-fanhuidingbu41:before { content: "\e64b"; }

  .icon-control101:before { content: "\e660"; }

  .icon-fanhuidingbu5:before { content: "\e70b"; }

  .icon-fanhuidingbu6:before { content: "\e633"; }

  .icon-fanhuidingbu7:before { content: "\e612"; }

  .icon-stack:before { content: "\e616"; }

  .icon-fanhuidingbu8:before { content: "\e619"; }

  .icon-fanhuidingbu9:before { content: "\e658"; }

</style>



